:root {
    --glass: rgba(255, 255, 255, 0.9);
    --glass-crystal: rgba(255, 255, 255, 0.5);
    --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
}

*{
    margin: 0;
    padding: 0;
    font-family: "Gill Sans", sans-serif;
}

.home-bullet-points {
    font-size: 1.15rem;
}

.home-column {
    float: left;
    width: 33.33%;
    padding: 5px;
}

.home-row {
    display: flex;
}
  
/* Clear floats after image containers */
.home-row::after {
    content: "";
    clear: both;
    display: table;
  }

#home-div-1{
    height: 280px !important;
    min-height: 280px !important;
    padding-top: 2%;
    padding-left: 2%;
    padding-right: 2%;
}

#home-div-1-left{}

#salary-home-card {
    text-align: center;
    padding-top: 2%;
    padding-bottom: 7%;
    border-radius: 7px;
}

#home-div-1-right {}

#pay-home-card {
    text-align: center;
    padding-top: 2%;
    padding-bottom: 7%;
    padding-left: 2%;
    padding-right: 2%;
    border-radius: 7px;
}

#home-div-1-left-demo-btn {
    margin-top: 2%;
    background-color: var(--glass-crystal) !important;
}

#home-div-1-right-contact-btn {
    margin-top: 2%;
    background-color: var(--glass-crystal) !important;
}

#home-div-2 {
    margin-top: -3%;
    padding-left: 2%;
    padding-right: 2%;
    min-height: 375px !important;
}

#home-div-2-right {
    text-align: right;
}

