#chart-snap {
    position: relative;
    z-index: 1;
    display: inline-block;
    border-radius: 5px;
    background-size: cover;
    background-image: url('../../public/images/charts_snap.png');   
    height: 300px;
    max-height: 300px;
    width: 70%;
    max-width: 70%;
}

#paylens-snap {
    position: relative;
    z-index: 1;
    display: inline-block;
    border-radius: 5px;
    background-size: cover;
    background-image: url('../../public/images/Group-137.png');   
    height: 300px;
    max-height: 300px;
    width: 83%;
    max-width: 83%;
}

#home-div-3 {   
    margin-top: -2%;
    padding-top: 2%;
    padding-left: 3%;
    padding-right: 2%;
    background-color: #a1cbdf;
    height: 470px;
    overflow: auto;
}

@media (min-width: 320px) and (max-width: 480px) {
    .home-product-card-heading {
        font-size: 1.3rem;
    }

    #home-div-1{
        padding-top: 4%;
    }

    #home-div-1-left-demo-btn {
        margin-top: 15%;
    }
    
    #salary-home-card {
        padding-bottom: 9%;
    }

    #pay-home-card {
        padding-bottom: 9%;
    }

    #home-div-3 { 
        margin-top: -5%;
        padding-bottom: 8%;
    }
}
